.contenedor-testimonio{
    width:  1170px;
    height: 368px;
    margin: 10px 10px 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #efefef;
    box-shadow: 0 3px 13px 1px rgb(0 0 0 / 9%);
}

.img-testimonio{
    height: 90%;
    width: 20rem;
}
.texto-testimonio{
    text-align: center;
    padding: 40px;
    text-align: center;
    font-family: Lato,sans-serif;
    font-weight: 400;
    line-height: 1.5rem;
}
.nbre{
    font-size: 1.3rem;
}
.titulo{
    font-size: 1.3rem;
    margin-top: 15px;
    padding-bottom: 30px;
}
.txt{
    font-size: 1.1rem;
    text-align: justify;
}