*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  html,body{
    font-size: 18px;
    background-color: #f5f6f7;
  }
  h1{
    font-size: 2.5em;
    font-family: Lato, sans-serif;
    font-weight: 700;
    line-height: 1.1;
    margin: 40px 0;
    text-align: center;
  }
  .App{
    min-height: 100vh;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  .contenedor-principal{
    min-height: 600px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    text-align: center;
  }